import React, {Component} from "react";
import {PropTypes} from "prop-types";

export const DesktopPriceBox = (props) => {
    let [head, ...tail] = props.prices;
    let paddingClass = props.biggerPadding ? "u-padding-all-m " : "u-padding-all ";
    return (
        <div className={paddingClass+"u-small-no-padding"}>
            <DesktopHeadPrice head={head}/>
            {tail && tail.length>0 ? <PriceTiers prices={tail}/> : null}
        </div>
    );
}

DesktopPriceBox.propTypes = {
    prices: PropTypes.array,
    biggerPadding: PropTypes.bool
}

DesktopPriceBox.defaultProps = {
    biggerPadding: true
}

export const MobilePriceBox = (props) => {
    let [head, ...tail] = props.prices;
    return (
        <div>
            <MobileHeadPrice head={head}/>
            {tail && tail.length>0 ? <PriceTiers prices={tail}/> : null}
        </div>
    );
}

const PriceTiers = (props) => {
    let [head, ...tail] = props.prices;
    return (
        <div>
            <p>
                <span>{head.integer},{head.fraction} {head.currency} </span>
                {head.end ? <span>{head.start}-{head.end} mies.</span> : <span>od {head.start} mies.</span>}
            </p>
            {tail && tail.length>0 ? <PriceTiers prices={tail}/> : null}
        </div>
    );
}

export const RwdPriceBox = (props) => {

    function getQuantityInfo() {
        if(isNaN(props.quantity) || props.quantity < 2){
            return null;
        }
        return (
            <span>
                <span className="opl-price-v2__whole u-small-font-standard">{props.quantity}</span>
                <span className="opl-price-v2__suffix u-small-font-standard"> razy </span>
            </span>
        );
    };

    let monthly = true;
    let [head, ...tail] = props.prices;
    const hasTail = tail && tail.length > 0;
    if(!props.prices || props.prices.length === 0 || (head.integer === "0" && head.fraction === "00" && !hasTail)){
        if (!head.start) {
            monthly = false;
        }
        head = props.oneTimePrice.integer !== "0" ? props.oneTimePrice : props.checkoutPrice;
        if (head.integer !== "0" || head.fraction !== "00") {
            monthly = false;
        }
    }
    return (
        <div>
            <p className="opl-price-v2 opl-price-v2--s g-small-brand2-c">
                <span className="opl-price-v2__part u-small-font-standard">
                    {
                        getQuantityInfo()
                    }
                    <span className="opl-price-v2__whole u-small-font-standard">{head.integer}</span>
                </span>
                <span className="opl-price-v2__part u-small-font-standard">
                    <span className="opl-price-v2__separator u-small-font-standard">,</span>
                    <span className="opl-price-v2__decimal u-small-font-standard">{head.fraction}</span>
                    <span className="opl-price-v2__suffix u-small-font-standard">{head.currency}{monthly ? "/mies." : ""}</span>
                </span>
            </p>
            {hasTail && <PriceTiers prices={tail}/>}
        </div>
    );
};


const DesktopHeadPrice = (props) => {

    function onlyFirstMonth(head){
        return(
            <span> w pierwszym mies.</span>
        )
    }

    function moreThanFirstMonth(head){
        return(
            <span>
                <span>/mies. </span>
                {head.end ? <span>{head.start} - {head.end} mies.</span> : null}
            </span>
        )
    }

    let head = props.head
    return(
        <p className="h5 u-no-spacing">
            <span className="h3 u-no-spacing u-inline-block">{head.integer}</span>
            <span>,{head.fraction} {head.currency}</span>
            {(head.start == head.end) ? onlyFirstMonth(head) : moreThanFirstMonth(head)}
        </p>
    )
}

const MobileHeadPrice = (props) => {

    function onlyFirstMonth(head){
        return(
            <span className="g-brand1-c"> w pierwszym mies.</span>
        )
    }

    function moreThanFirstMonth(head){
        return(
            <span>
                <span className="g-brand1-c">/mies. </span>
                {head.end ? <span className="g-brand1-c">{head.start} - {head.end} mies.</span> : null}
            </span>
        )
    }

    let head = props.head
    return(
            <p>
                <span className="g-brand1-c">{head.integer},{head.fraction} {head.currency}</span>
                {(head.start == head.end) ? onlyFirstMonth(head) : moreThanFirstMonth(head)}
            </p>
    )
}